import React from "react";
import { FaLink } from "react-icons/fa";

const Cards = ({ projectLink, imgLink, alt, projectTitle, projectDesc }) => {
  return (
    <div>
      <a href={projectLink}>
        <div className="border shadow-2xl lg:hover:scale-110 transform rounded-2xl overflow-hidden">
          <img src={imgLink} alt={alt} />
          <div className="p-4 text-center">
            <p className="font-Poppins justify-center items-center flex">
              {projectTitle}
              <span className="mx-3">
                <FaLink fontSize="small" />
              </span>
            </p>
            <small className="font-Poppins">{projectDesc}</small>
          </div>
        </div>
      </a>
    </div>
  );
};

export default Cards;
