import React, { useState } from "react";
import {
  FaBars,
  FaTwitter,
  FaFacebook,
  FaLinkedin,
  FaGithub,
  FaTimes,
} from "react-icons/fa";
import "../App.css";
import { useTransition, animated } from "react-spring";
// import { NavLink } from "react-router-dom";
import { Link as NavLink } from "react-scroll";

