import React from "react";
import Input from "./formInput";

const ContactForm = () => {
  return (
    <form className="w-full flex-1 md:w-full md:w-10/12 mx-auto">
      <div className="md:max-w-md">
        <div className="grid grid-cols-1 gap-6">
          <Input type="email" Content="Name" placeholder="Enter Name" />

          <Input
            type="email"
            Content="Email Address"
            placeholder="john@example.com"
          />

          <label className="block">
            <span className="text-gray-700">Get In Touch</span>
            <textarea
              className="mt-1 block w-full rounded-md bg-gray-100 border-transparent focus:border-gray-500 focus:bg-white focus:ring-0"
              rows="4"
            ></textarea>
          </label>

          <div>
            <button className="bg-gray-700 text-white rounded-full hover:bg-gray-500 py-3 px-7">
              Submit
            </button>
          </div>
        </div>
      </div>
    </form>
  );
};

export default ContactForm;
