import React from "react";

const formInput = ({ Content, placeholder }) => {
  return (
    <div>
      <label className="block">
        <span className="text-gray-700">{Content}</span>
        <input
          type="email"
          className="mt-1 block w-full py-4 px-4 rounded-md bg-gray-100 border-transparent focus:border-gray-500 focus:bg-white focus:ring-0"
          placeholder={placeholder}
        />
      </label>
    </div>
  );
};

export default formInput;
