import React from "react";
import Ustacky from "../Assets/Ecommerce.png";
import Dictionary from "../Assets/Dictionary.png";
import HNG2 from "../Assets/Hng2.png";
import { FaAngleDoubleDown } from "react-icons/fa";
import Bounce from "react-reveal/Bounce";
import Cards from "./Cards";

const Projects = () => {
  return (
    <div
      className="w-10/12 justify-center mt-10 space-y-4 lg:space-y-12 mx-auto"
      id="Project"
    >
      <h1 className="text-xl flex items-center justify-center md:text-2xl lg:text-3xl text-gray-600 font-bold font-Poppins my-10">
        Recent Projects
        <span className="mx-2 animate-ping text-sm">
          <FaAngleDoubleDown />
        </span>
      </h1>
      <Bounce cascade>
        <div className="grid cursor-pointer gap-y-6 gap-x-4 transform scale-90 lg:gap-y-0 grid-col-1 sm:grid-cols-2  md:grid-cols-2 lg:grid-cols-3">
          <Cards
            projectLink={"https://ustackyecommerce.netlify.app"}
            alt={"ustacky"}
            imgLink={Ustacky}
            projectTitle={"An E-commerce Website"}
            projectDesc={
              "Built with HTML, CSS & Javacript; Functional programing, Integrated with Paystack for Payment"
            }
          />

          <Cards
            projectLink={"https://react-dict.netlify.app"}
            alt={"Dictionary"}
            imgLink={Dictionary}
            projectTitle={" A Dictionary App"}
            projectDesc={
              " Built with Audio Support and Dark-Mode Toggle; using ReactJs, axios Google Api, Tailwind CSS"
            }
          />

          <Cards
            projectLink={"https://hngprojectsite.netlify.app"}
            alt={"HNG2"}
            imgLink={HNG2}
            projectTitle={"A Complete Website"}
            projectDesc={
              " Implemented from a Figma Design using HTML, CSS & Javacript(Fully Responsive)"
            }
          />
        </div>
      </Bounce>
    </div>
  );
};

export default Projects;
